.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand {
  transform-origin: bottom;
  transform: translateX(-50%) rotate 0deg;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__hand--min {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch__hand--sec {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hand--min {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand--sec {
  animation: 10s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

html, body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
